import React from "react";
import { apiRequest } from "../../utils/helper";
import {
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  ComposedChart,
  Area
} from "recharts";
import "./Charts.css";
import { FormControl, TextField, Fab, Button } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
export default function Charts({ date, index, type, ce, pe }) {
  const [chartData, setChartData] = React.useState([]);
  const [chartData2, setChartData2] = React.useState([]);
  const [point, setPoint] = React.useState(21);
  const [point2, setPoint2] = React.useState(21);
  const [callLtp, setCallLtp] = React.useState(true);
  const [putLtp, setPutLtp] = React.useState(true);
  const [callputLtp, setCallputLtp] = React.useState(true);
  const [putcallLtp, setputcallLtp] = React.useState(false);
  React.useEffect(() => {
    const authHandler = async () => {
      try {
        const userData = await apiRequest({
          url: "https://api.optionalgo.in:6001/v1/getdata",
          method: "post",
          bodyParams: {
            date: date,
            pointtype: type,
            stockname: index,
            strike_price_pe: pe,
            strike_price_ce: ce
          }
        });
        const { data, success } = userData;
        if (success) {
          let cData = [];
          let peak = 0;
          let low = 0;
          let points = point;
          console.log(data.length);
          for (let i = 0; i < data.length; i++) {
            let { time_hour_m_in_sec, indexprice, stock } = data[i];

            for (let j = 0; j < stock.length; j++) {
              let data2 = {};

              if (type !== "Both") {
                let {
                  last_price,
                  oi,
                  expiry,
                  strike_price,
                  instrument_type
                } = stock[j];
                data2 = {
                  id: time_hour_m_in_sec,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  last_price: last_price.toFixed(2),
                  oi: oi,
                  expiry: expiry,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else {
                let {
                  last_price,
                  oi,
                  oice,
                  oipe,
                  strike_price_pe,
                  strike_price_ce,
                  last_price_ce,
                  last_price_pe,
                  instrument_type
                } = stock[j];
                if (point === 0) {
                  peak = indexprice.toFixed(2);
                  low = indexprice.toFixed(2);
                } else if (points === point || points === 0) {
                  points = point;
                  peak = last_price.toFixed(2);
                  low = last_price.toFixed(2);
                  points = points - 1;
                } else {
                  if (last_price > peak) {
                    peak = last_price.toFixed(2);
                  }
                  if (last_price < low) {
                    low = last_price.toFixed(2);
                  }
                  points = points - 1;
                }
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  last_price: last_price,
                  indexprice: indexprice,
                  oi: oi,
                  oi_call: oice,
                  oi_puts: oipe,
                  strike_price_ce: strike_price_ce,
                  strike_price_pe: strike_price_pe,
                  last_price_ce: last_price_ce,
                  last_price_pe: last_price_pe,
                  last_price_percentage_ce__pe: (
                    (last_price_ce / last_price_pe) *
                    100
                  ).toFixed(2),
                  last_price_percentage_pe__ce: (
                    (last_price_pe / last_price_ce) *
                    100
                  ).toFixed(2),
                  instrument_type: instrument_type,
                  height: [low, peak]
                };
              }

              cData.push(data2);
            }
          }

          setChartData2([...cData]);
        }
      } catch (err) {
        console.log(err);
      }
    };
    authHandler();
    const interval = setInterval(() => {
      authHandler();
    }, 60000);
    return () => clearInterval(interval);
  }, [date, index, type, ce, pe, point]);

  React.useEffect(() => {
    setChartData([]);
    setChartData2([]);
  }, [date, index, type, ce, pe, point]);

  React.useEffect(() => {
    const updatechart = () => {
      if (chartData.length === 0) {
        setChartData(chartData2);
      } else {
        for (let i = chartData.length; i < chartData2.length; i++) {
          setChartData([...chartData, chartData2[i]]);
        }
      }
    };
    updatechart();
  }, [chartData2, chartData]);
  const customTooltipOnYourLine = e => {
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type !== "Both"
    ) {
      return (
        <div className="custom-tooltip">
          <p>{e.payload[0].payload["time"]}</p>
          <p>{e.payload[0].payload["last_price"]}</p>
          <p>{e.payload[0].payload["strike_price"]}</p>
          <p>{e.payload[0].payload["indexprice"]}</p>
        </div>
      );
    }
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type === "Both"
    ) {
      return (
        <div className="customTooltip">
          <div className="tooltipContent">
            <p>Time: {e.payload[0].payload["time"]}</p>
            <p>LTP: {e.payload[0].payload["last_price"]}</p>
            <p>Puts Last Price: {e.payload[0].payload["last_price_pe"]}</p>
            <p>Call Last Price: {e.payload[0].payload["last_price_ce"]}</p>
            <p>
              Percentage Call/Puts:{" "}
              {e.payload[0].payload["last_price_percentage_ce__pe"]}
            </p>
            <p>Low: {e.payload[0].payload["height"][0]}</p>
            <p>Peak: {e.payload[0].payload["height"][1]}</p>
          </div>
        </div>
      );
    } else {
      return "";
    }
  };
  const customTooltipOnYourLine2 = e => {
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type !== "Both"
    ) {
      return (
        <div className="custom-tooltip">
          <p>{e.payload[0].payload["time"]}</p>
          <p>{e.payload[0].payload["last_price"]}</p>
          <p>{e.payload[0].payload["strike_price"]}</p>
          <p>{e.payload[0].payload["indexprice"]}</p>
        </div>
      );
    }
    if (
      e.active &&
      e.payload != null &&
      e.payload[0] != null &&
      type === "Both"
    ) {
      return (
        <div className="customTooltip">
          <div className="tooltipContent">
            <p>LTP: {e.payload[0].payload["last_price"]}</p>
            <p>Puts Price: {e.payload[0].payload["strike_price_pe"]}</p>
            <p>Call Price: {e.payload[0].payload["strike_price_ce"]}</p>
            <p>Time: {e.payload[0].payload["time"]}</p>
            <p>Low: {e.payload[0].payload["height"][0]}</p>
            <p>Peak: {e.payload[0].payload["height"][1]}</p>
          </div>
        </div>
      );
    } else {
      return "";
    }
  };

  return (
    <div>
      <ResponsiveContainer width={1000} height={400}>
        <ComposedChart
          width={1000}
          height={400}
          data={chartData}
          margin={{ top: 5, right: 20, bottom: 5, left: 0 }}
        >
          {point >= 2 ? (
            <Area dataKey="height" stroke=" #e65c00" fill="#ffa366" />
          ) : null}
          <Line type="monotone" dataKey="last_price" stroke="#8884d8" />
          {callLtp ? (
            <Line type="monotone" dataKey="last_price_ce" stroke="#fb3a1c" />
          ) : null}
          {putLtp ? (
            <Line type="monotone" dataKey="last_price_pe" stroke="black" />
          ) : null}
          {callputLtp ? (
            <Line
              type="monotone"
              dataKey="last_price_percentage_ce__pe"
              stroke="green"
            />
          ) : null}
          {putcallLtp ? (
            <Line
              type="monotone"
              dataKey="last_price_percentage_pe__ce"
              stroke="green"
            />
          ) : null}
          <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
          <XAxis dataKey="time" />
          <YAxis
            type="number"
            allowDecimals={true}
            domain={["dataMin-100", "dataMax+100"]}
          />
          <Tooltip />
        </ComposedChart>
      </ResponsiveContainer>
      <div>
        <FormControl
          variant="outlined"
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-evenly",
            padding: "10px"
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "row"
            }}
          >
            <TextField
              id="outlined-basic"
              label="Points"
              variant="outlined"
              defaultValue={21}
              onChange={e => {
                setPoint2(e.target.value);
              }}
              style={{ padding: "1%" }}
            />
            <Fab
              color="primary"
              aria-label="add"
              onClick={() => {
                setPoint(point2);
              }}
              style={{ padding: "1%" }}
            >
              <AddIcon
                onClick={() => {
                  setPoint(point2);
                }}
              />
            </Fab>
          </div>
          {callLtp ? (
            <Button
              variant="contained"
              size="medium"
              color="primary"
              onClick={() => {
                setCallLtp(!callLtp);
              }}
              style={{ padding: "3px" }}
            >
              {" "}
              Calls LTP
            </Button>
          ) : (
            <Button
              variant="outlined"
              size="medium"
              color="primary"
              onClick={() => {
                setCallLtp(!callLtp);
              }}
              style={{ padding: "3px" }}
            >
              {" "}
              Calls LTP
            </Button>
          )}

          {putLtp ? (
            <Button
              variant="contained"
              size="medium"
              color="primary"
              onClick={() => {
                setPutLtp(!putLtp);
              }}
              style={{ padding: "3px" }}
            >
              Puts LTP
            </Button>
          ) : (
            <Button
              variant="outlined"
              size="medium"
              color="primary"
              onClick={() => {
                setPutLtp(!putLtp);
              }}
              style={{ padding: "3px" }}
            >
              Puts LTP
            </Button>
          )}

          {callputLtp ? (
            <Button
              variant="contained"
              size="medium"
              color="primary"
              onClick={() => {
                setCallputLtp(!callputLtp);
              }}
              style={{ padding: "3px" }}
            >
              Calls%Puts LTP
            </Button>
          ) : (
            <Button
              variant="outlined"
              size="medium"
              color="primary"
              onClick={() => {
                setCallputLtp(!callputLtp);
              }}
              style={{ padding: "3px" }}
            >
              Calls%Puts LTP
            </Button>
          )}

          {putcallLtp ? (
            <Button
              variant="contained"
              size="medium"
              color="primary"
              onClick={() => {
                setputcallLtp(!putcallLtp);
              }}
              style={{ padding: "3px" }}
            >
              Putss%Calls LTP
            </Button>
          ) : (
            <Button
              variant="outlined"
              size="medium"
              color="primary"
              onClick={() => {
                setputcallLtp(!putcallLtp);
              }}
              style={{ padding: "3px" }}
            >
              Putss%Calls LTP
            </Button>
          )}
        </FormControl>
        <ResponsiveContainer width={1000} height={400}>
          <ComposedChart
            width={1000}
            height={400}
            data={chartData}
            margin={{ top: 5, right: 20, bottom: 5, left: 15 }}
          >
            <Line type="monotone" dataKey="oi" stroke="#fb3a1c" />
            <Line type="monotone" dataKey="oi_puts" stroke="black" />
            <Line type="monotone" dataKey="oi_call" stroke="green" />
            <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
            <XAxis dataKey="time" />
            <YAxis
              type="number"
              allowDecimals={true}
              allowDataOverflow
              domain={["dataMin-1000", "dataMax+10"]}
            />
            <Tooltip />
          </ComposedChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
