import React from "react";
import { apiRequest } from "../../utils/helper";
import {
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  ComposedChart,
  Area
} from "recharts";
import "./Charts.css";
import { FormControl, TextField, Fab } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";

export default function Charts2({ date, index, sPrice, lPrice }) {
  const [chartData, setChartData] = React.useState([]);
  const [chartData2, setChartData2] = React.useState([]);
  const [point, setPoint] = React.useState(21);
  const [point2, setPoint2] = React.useState(21);

  React.useEffect(() => {
    setChartData([]);
    setChartData2([]);
  }, [index, date, point]);
  React.useEffect(() => {
    const authHandler = async () => {
      try {
        console.log(date, index);
        const userData = await apiRequest({
          url: "https://api.optionalgo.in:6001/v1/getdata",
          method: "post",
          bodyParams: {
            date: date,
            pointtype: "CE",
            stockname: index
          }
        });
        const { data, success } = userData;

        if (success) {
          let cData = [];
          let peak = 0;
          let low = 0;
          let points = point;

          for (let i = 0; i < data.length; i++) {
            let { time_hour_m_in_sec, indexprice } = data[i];

            if (i === 0) {
              sPrice(indexprice);
            }
            if (i === data.length - 1) {
              lPrice(indexprice);
            }
            if (point === 0) {
              peak = indexprice;
              low = indexprice;
            } else if (points === point || points === 0) {
              points = point;
              peak = indexprice;
              low = indexprice;
              points = points - 1;
            } else {
              if (indexprice > peak) {
                peak = indexprice;
              }
              if (indexprice < low) {
                low = indexprice;
              }
              points = points - 1;
            }

            cData.push({
              time: time_hour_m_in_sec,
              price: indexprice,
              height: [low, peak]
            });
          }
          setChartData2(cData);
        }
      } catch (err) {
        window.alert(err);
      }
    };
    authHandler();

    const interval = setInterval(() => {
      authHandler();
    }, 60000);
    return () => clearInterval(interval);
  }, [date, index, sPrice, lPrice, point]);

  React.useEffect(() => {
    const updatechart = () => {
      if (chartData.length === 0) {
        setChartData(chartData2);
      } else {
        for (let i = chartData.length; i < chartData2.length; i++) {
          setChartData([...chartData, chartData2[i]]);
        }
      }
    };
    updatechart();
  }, [chartData2, chartData]);

  const customTooltipOnYourLine = e => {
    if (e.active && e.payload != null && e.payload[0] != null) {
      return (
        <div className="customTooltip">
          <div className="tooltipContent">
            <p>Price: {e.payload[0].payload["price"]}</p>
            <p>Time: {e.payload[0].payload["time"]}</p>
            <p>Low: {e.payload[0].payload["height"][0]}</p>
            <p>Peak: {e.payload[0].payload["height"][1]}</p>
          </div>
        </div>
      );
    } else {
      return "";
    }
  };

  return (
    <div>
      <div>
        <br />
      </div>

      <ResponsiveContainer width={1000} height={400}>
        <ComposedChart
          width={1000}
          height={400}
          data={chartData}
          margin={{ top: 5, right: 20, bottom: 5, left: 0 }}
        >
          {point >= 2 ? (
            <Area dataKey="height" stroke="#8884d8" fill="#8884d8" />
          ) : null}
          <Line type="monotone" dataKey="price" stroke="#8884d8" />
          <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
          <XAxis dataKey="time" />
          <YAxis
            type="number"
            allowDecimals={true}
            allowDataOverflow
            domain={["dataMin-10", "dataMax+10 "]}
          />
          <Tooltip content={customTooltipOnYourLine} />
        </ComposedChart>
      </ResponsiveContainer>
      <div>
        <FormControl
          variant="outlined"
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "center"
          }}
        >
          <TextField
            id="outlined-basic"
            label="Points"
            variant="outlined"
            defaultValue={21}
            onChange={e => {
              setPoint2(e.target.value);
            }}
            style={{ padding: "1%" }}
          />
          <Fab
            color="primary"
            aria-label="add"
            onClick={() => {
              setPoint(point2);
            }}
            style={{ padding: "1%" }}
          >
            <AddIcon
              onClick={() => {
                setPoint(point2);
              }}
            />
          </Fab>
        </FormControl>
      </div>
    </div>
  );
}
