import React from "react";
import { apiRequest } from "../../utils/helper";
import {
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  ComposedChart
} from "recharts";
import "./Charts.css";

export default function Charts3({ date, index, type, ce, pe }) {
  const [chartData, setChartData] = React.useState([]);
  const [chartData2, setChartData2] = React.useState([]);

  React.useEffect(() => {
    const authHandler = async () => {
      try {
        const userData = await apiRequest({
          url: "https://api.optionalgo.in:6001/v1/getoidata",
          method: "post",
          bodyParams: {
            date: date,
            stockname: index,
            strike_price_pes: pe,
            strike_price_ces: ce
          }
        });
        const { data, success } = userData;
        console.log(userData);
        if (success) {
          let cData = [];

          for (let i = 0; i < data.length; i++) {
            let { time_hour_m_in_sec, indexprice, stock } = data[i];

            for (let j = 0; j < stock.length; j++) {
              let data2 = {};

              let { oi, strike_price, instrument_type } = stock[j];
              if (ce[0] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi0: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (ce[1] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi1: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (ce[2] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi2: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (ce[3] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi3: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (ce[4] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi4: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (pe[0] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi5: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (pe[1] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi6: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (pe[2] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi7: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (pe[3] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi8: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              } else if (pe[4] === strike_price) {
                data2 = {
                  id: cData.length,
                  time: time_hour_m_in_sec,
                  indexprice: indexprice,
                  oi9: oi,
                  strike_price: strike_price,
                  instrument_type: instrument_type
                };
              }
              console.log(cData);
              cData.push(data2);
            }
          }
          setChartData2(cData);
          console.log(cData);
        }
      } catch (err) {
        window.alert(err);
      }
    };
    authHandler();
    const interval = setInterval(() => {
      authHandler();
    }, 60000);
    return () => clearInterval(interval);
  }, [date, index, type, ce, pe]);

  React.useEffect(() => {
    setChartData([]);
    setChartData2([]);
  }, [date, index, type, ce, pe]);

  React.useEffect(() => {
    const updatechart = () => {
      if (chartData.length === 0) {
        setChartData(chartData2);
      } else {
        for (let i = chartData.length; i < chartData2.length; i++) {
          setChartData([...chartData, chartData2[i]]);
        }
      }
    };
    updatechart();
  }, [chartData2, chartData]);

  const customTooltipOnYourLine = e => {
    if (e.active && e.payload != null && e.payload[0] != null) {
      return (
        <div className="customTooltip">
          <div className="tooltipContent">
            <p>OI: {e.payload[0].payload["OI"]}</p>
            <p>Strike Price: {e.payload[0].payload["strike_price"]}</p>
            <p>Time: {e.payload[0].payload["time"]}</p>
          </div>
        </div>
      );
    } else {
      return "";
    }
  };

  return (
    <div>
      <div>
        {ce !== undefined && ce[0] !== undefined ? (
          <h5 style={{ background: "#194610", color: "white" }}>CE: {ce[0]}</h5>
        ) : null}
        {ce !== undefined && ce[1] !== undefined ? (
          <h5 style={{ background: "#1f770d", color: "white" }}>CE: {ce[1]}</h5>
        ) : null}
        {ce !== undefined && ce[2] !== undefined ? (
          <h5 style={{ background: "#a2c764", color: "white" }}>CE: {ce[2]}</h5>
        ) : null}
        {ce !== undefined && ce[3] !== undefined ? (
          <h5 style={{ background: "#dff54f", color: "white" }}>CE: {ce[3]}</h5>
        ) : null}
        {ce !== undefined && ce[4] !== undefined ? (
          <h5 style={{ background: "#f5f04f", color: "white" }}>CE: {ce[4]}</h5>
        ) : null}
        {pe !== undefined && pe[0] !== undefined ? (
          <h5 style={{ background: "#ab6988", color: "white" }}>PE: {pe[0]}</h5>
        ) : null}
        {pe !== undefined && pe[1] !== undefined ? (
          <h5 style={{ background: "#010b28", color: "white" }}>PE: {pe[1]}</h5>
        ) : null}
        {pe !== undefined && pe[2] !== undefined ? (
          <h5 style={{ background: "#0018ff", color: "white" }}>PE: {pe[2]}</h5>
        ) : null}
        {pe !== undefined && pe[3] !== undefined ? (
          <h5 style={{ background: "#24756c", color: "white" }}>PE: {pe[3]}</h5>
        ) : null}
        {pe !== undefined && pe[4] !== undefined ? (
          <h5 style={{ background: "#906c59", color: "white" }}>PE: {pe[4]}</h5>
        ) : null}
      </div>
      <ComposedChart
        width={1200}
        height={500}
        data={chartData}
        margin={{ top: 5, right: 20, bottom: 5, left: 20 }}
      >
        {ce !== undefined && ce[0] !== undefined ? (
          <Line type="monotype" dataKey="oi0" stroke="#194610" />
        ) : null}
        {ce !== undefined && ce[1] !== undefined ? (
          <Line type="monotype" dataKey="oi1" stroke="#1f770d" />
        ) : null}
        {ce !== undefined && ce[2] !== undefined ? (
          <Line type="monotype" dataKey="oi2" stroke="#a2c764" />
        ) : null}
        {ce !== undefined && ce[3] !== undefined ? (
          <Line type="monotype" dataKey="oi3" stroke="#dff54f" />
        ) : null}
        {ce !== undefined && ce[4] !== undefined ? (
          <Line type="monotype" dataKey="oi4" stroke="#f5f04f" />
        ) : null}
        {pe !== undefined && pe[0] !== undefined ? (
          <Line type="monotype" dataKey="oi5" stroke="#ab6988" />
        ) : null}
        {pe !== undefined && pe[1] !== undefined ? (
          <Line type="monotype" dataKey="oi6" stroke="#010b28" />
        ) : null}
        {pe !== undefined && pe[2] !== undefined ? (
          <Line type="monotype" dataKey="oi7" stroke="#0018ff" />
        ) : null}
        {pe !== undefined && pe[3] !== undefined ? (
          <Line type="monotype" dataKey="oi8" stroke="#24756c" />
        ) : null}
        {pe !== undefined && pe[4] !== undefined ? (
          <Line type="monotype" dataKey="oi9" stroke="#906c59" />
        ) : null}
        <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
        <XAxis dataKey="time" />
        <YAxis
          type="number"
          allowDecimals={true}
          allowDataOverflow
          domain={["dataMin-1000", "dataMax+1000"]}
        />
        <Tooltip />
      </ComposedChart>
    </div>
  );
}
