import React from "react";
import { HashRouter as Router, Route } from "react-router-dom";

import Home from "./Home";
import OIPage from "./OIPage";
const Root = () => {
  return (
    <div>
      <Router basename="/">
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/OI" component={OIPage} />
      </Router>
    </div>
  );
};

export default Root;
